import { ResultsData } from './types';

// Backend configuration
const config = {
  development: {
    backendUrl: 'http://localhost:3000'
  },
  production: {
    // In production, use relative URL since everything is served from the same origin
    backendUrl: '/api'
  },
  // Special case for GitHub Pages (static hosting)
  githubPages: {
    mockData: {
      // Generate two weeks of mock data
      ...Object.fromEntries(
        Array.from({ length: 14 }, (_, i) => {
          const date = new Date();
          date.setDate(date.getDate() - i);
          return [
            date.toLocaleDateString('en-GB').replace(/\//g, '/'),
            [
              { type: 'account', test_cases_count: '15', status: Math.random() > 0.2 ? 'passed' : 'failed' as const },
              { type: 'charges', test_cases_count: '25', status: Math.random() > 0.2 ? 'passed' : 'failed' as const },
              { type: 'payments', test_cases_count: '30', status: Math.random() > 0.2 ? 'passed' : 'failed' as const },
              { type: 'refunds', test_cases_count: '20', status: Math.random() > 0.2 ? 'passed' : 'failed' as const },
              { type: 'disputes', test_cases_count: '10', status: Math.random() > 0.2 ? 'passed' : 'failed' as const }
            ]
          ];
        })
      )
    } as ResultsData
  }
};

// Check if we're running on GitHub Pages
const isGitHubPages = window.location.hostname.includes('github.io');

// Get the backend URL based on environment
export const getBackendUrl = async () => {
  // If on GitHub Pages, return mock data
  if (isGitHubPages) {
    return Promise.resolve({
      isMock: true,
      mockData: config.githubPages.mockData
    });
  }

  const baseUrl = import.meta.env.DEV ? config.development.backendUrl : config.production.backendUrl;
  
  try {
    // Test the connection
    await fetch(`${baseUrl}/results`);
    return {
      isMock: false,
      url: baseUrl
    };
  } catch (error) {
    console.error('Backend not available:', error);
    throw new Error(`Backend server is not available at ${baseUrl}`);
  }
}; 